import React, { useState, useEffect } from 'react'
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom'
import dayjs from 'dayjs'
import liff from '@line/liff'
import Item from '@pages/Item'
import Team from '@pages/Team'
import TeamForm from '@pages/TeamForm'
import TeamDB from '@model/team.model'

import { getAttractions } from '@utils/fetchAttractions'

function App() {
  const [isLogin] = useState(true)
  const [user, setUser] = useState({})
  const [team, setTeam] = useState({})

  useEffect(async () => {
    await liff.init({ liffId: process.env.REACT_APP_LIFFID, withLoginOnExternalBrowser: true })
    if (!liff.isLoggedIn()) liff.login()
    setUser(await liff.getProfile())
  }, [])

  useEffect(() => {
    setTeam(TeamDB.getAmount(user.id))
  }, [])

  const addTeamMessage = async data => {
    await liff.ready
    const { extraId, location, user, memberAmount, members, memberNonLimit, time_start, time_end } = data
    const contextType = (liff.getContext() || {}).type
    const isInChat = ~['utou', 'room', 'group'].indexOf(contextType)
    if (!isInChat) alert('You are not in chatroom')
    try {
      liff
        .sendMessages([
          {
            type: 'text',
            text: `各位揪友好！我是${data.user.displayName}。將會舉辦從${dayjs(time_start).format(
              'YYYY/MM/DD'
            )} 至 ${dayjs(time_end).format(
              'YYYY/MM/DD'
            )} 活動，地點在 ${location}，人數需要 ${memberAmount}人，若有興趣，歡迎報名！`
          }
        ])
        .then(() => alert('訊息已發送！'))
    } catch (err) {
      console.error('Error: ', err)
      alert(err)
    }
  }

  return (
    <div className="container">
      <Router basename={process.env.NODE_ENV === 'development' ? '' : '/TravelFunSite'}>
        <Routes>
          <Route
            exact
            path="/"
            element={
              isLogin &&
              getAttractions(5).map(item => <Item key={item.Id} data={item} amount={team[item.Id] || false} />)
            }
          />
          <Route path="/:id/registration" element={<TeamForm user={user} addTeamMessage={addTeamMessage} />} />
          <Route path="/:id/teams" element={<Team userId={user.id} />} />
        </Routes>
      </Router>
    </div>
  )
}
export default App
